.title-icons{
    display:flex;
    justify-content: space-between;
}

.icons{
    display:flex;
    justify-content: space-between;
    gap:20px;
}

@media (max-width: 768px) {
    .title-icons{
        flex-direction: column;
        justify-content: flex-start;
    }
    .icons{
        display:flex;
        justify-content: flex-start;
        gap:5px;
    }
  }


.screenshots-container{
    background-color: rgb(239, 238, 238);
    margin-top:50px;
    margin-bottom:50px;
    padding:30px;
}
@media (max-width: 480px) {
    .screenshots-container{
        padding:1rem;
        margin:0px;
    }

  }
.screenshots{
    display:flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap:20px;

}

@media (max-width: 480px) {
    .screenshots{
        flex-direction: column;
        justify-content: flex-start;
    }

  }


.screenshot {
    flex-basis: calc(50% - 20px);
    /* 10px accounts for half the gap between two items */
  }

.screenshot img {
    object-position: left;
}

.screenshot:first-child:nth-last-child(odd) {
    flex-basis: 100%;
  }

.project{
    margin-bottom:200px;
}