.education-school-box{
    display:flex;
    flex-direction:row;
    padding-bottom:50px;
}

img{
    width:100%; 
    height:100%; 
    object-fit: cover;
}

.tse-image{
    flex:1.5;
}

.tse-text{
    flex:2;
    padding-left: 40px;
    display:flex;
    flex-direction:column;
    justify-content: space-between;
    text-align: left;
}

.tse-text-first-half{
    align-self:flex-start;
}
.tse-text-second-half{
    align-self:flex-start;
}

.rhul-image{
    flex:1;
}

.rhul-text{
    flex:1;
    padding-right: 40px;
    display:flex;
    flex-direction:column;
    justify-content: space-between;
    text-align: left;
}

.rhul-text-first-half{
    align-self:flex-start;
}
.rhul-text-second-half{
    align-self:flex-start;
}

.finance-box{
    display:flex;
    flex-direction:row;
    padding-bottom:20px;
}

.finance-image{
    flex:1;
}

.finance-text{
    flex:1;
    padding-left: 40px;
}

.icons-container{
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
    gap: 40px;
    padding-top: 20px;
}

@media (max-width: 768px) {
    .icons-container{
        justify-content: center;
        gap:10px;
        flex-wrap:wrap;

    }
  }

  @media (max-width: 480px) {
    .education-school-box{
        flex-direction: column;
    }
    .tse-text{
        padding-left: 0px;
    }
    .rhul-text{
        padding-right: 0px;
    }
    .finance-box{
        flex-direction: column;
    }
    .finance-text{
        padding-left: 0px;
    }
  }
